function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        spaceBetween: 0,
        loop: false,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
    })
}

$(document).ready(function() {

    sliderMain();

    // Menu
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Menu - Burger
    $('.header-mob__burger').click(function () {
        $(this).toggleClass('header-mob__burger--active')
        $('.header__wrapp').toggleClass('header__wrapp--active')
        $('body').toggleClass('fixed-body')

        $('.sidebar').toggleClass('sidebar--active')
    })

    // Sidebar - Burger
    $('.sidebar-burger').click(function () {
        $(this).toggleClass('sidebar-burger--active')
        $('.sidebar-menu').toggleClass('sidebar-menu--active')
        $('body').toggleClass('fixed-body')
    })

    // Accordeon
    $(".sidebar-item").click((function() {
        $(this).toggleClass("sidebar-item--active")
        $(this).children(".sidebar-hidden").slideToggle()}));

})

function onIn() {
    if (window.innerWidth > 991) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 991) {
        $(this).removeClass("header-bottom__item--active")
    }
}